.page {
  height: 100%;
  font-size: 16px;
  font-family: "Oswald", "Arial", sans-serif;
  line-height: 20px;
}

.page__body {
  min-height: 100%;
  margin: 0;
  background-color: #ffffff;
  color: #000000;
}

@media (min-width: @desktop-width) {
  .page__body {
    padding: 0 110px;
    padding-top: 54px;
    box-sizing: border-box;
  }
}
